import React from "react";
import { useHistory } from "react-router";

function Home() {
  const history = useHistory();
  const click = (event) => {
    const {
      target: { id },
    } = event;
    history.push(id);
  };
  return (
    <>
      <h1>발로란트 정보</h1>
      <button onClick={click} id="agents">
        요원 정보
      </button>
      <button onClick={click} id="map">
        맵 정보
      </button>
      <button onClick={click} id="mode">
        게임 모드 정보
      </button>
      <button onClick={click} id="event">
        이벤트 정보
      </button>
    </>
  );
}

export default Home;
