import React, { useEffect, useState } from "react";

function Events() {
  const [data, setData] = useState([]);
  useEffect(async () => {
    await window
      .fetch("https://valorant-api.com/v1/events?language=ko-KR")
      .then(function (res) {
        res.json().then(function (json) {
          setData(json.data);
        });
      });
  }, []);
  console.log(data);
  return (
    <div>
      {data.map((res) => {
        return (
          <>
            <h1>{res.displayName}</h1>
            <h2>{res.shortDisplayName}</h2>
            <br />
            <br />
            <h5>이벤트 시작일 : {res.startTime}</h5>
            <h5>이벤트 종료일 : {res.endTime}</h5>
          </>
        );
      })}
    </div>
  );
}

export default Events;
