import React, { useEffect, useState } from "react";
import Info from "./info";
import Nav from "./nav";

function Age() {
  const [datas, setDatas] = useState([]);
  const [doc, setDoc] = useState([]);
  const [click, setClick] = useState(false);
  const [load, setLoad] = useState(true);
  useEffect(async () => {
    await window
      .fetch("https://valorant-api.com/v1/agents?language=ko-KR")
      .then(function (res) {
        res.json().then(function (json) {
          setDatas(json.data);
        });
      });
    setLoad(false);
  }, [doc]);

  const info = (event) => {
    const {
      target: { id },
    } = event;
    setDoc(id);
    setClick(true);
  };
  const noClick = () => {
    setClick(false);
  };
  return (
    <>
      <h1>요원 정보</h1>
      {load ? (
        <>Loading</>
      ) : (
        <>
          {click ? (
            <>
              <Info doc={doc} />
              <button onClick={noClick}>요원 목록</button>
            </>
          ) : (
            <>
              <ul>
                {datas.map((data) => {
                  return (
                    <>
                      <li onClick={info} id={data.uuid}>
                        <img
                          src={data.displayIcon}
                          style={({ maxWidth: 50 }, { maxHeight: 50 })}
                          id={data.uuid}
                          onClick={info}
                        />
                        {data.displayName}
                      </li>
                    </>
                  );
                })}
              </ul>
            </>
          )}
        </>
      )}
    </>
  );
}

export default Age;
