import React from "react";
import { useHistory } from "react-router";

function Nav() {
  const history = useHistory();
  const main = () => {
    history.push("/");
  };
  return (
    <ul>
      <li className="nav">
        <button onClick={main}>메인</button>
      </li>
    </ul>
  );
}

export default Nav;
